<template>
	<div class="placeOrder">
		<div class="placeOrder_head">
			<i class="icon_arrow_left_black" @click="goBack"></i>
			<p>提交订单</p>
		</div>
		<syfulizhi />
		<div class="placeOrder_box">
			<img :src="itemData.commodityMainImgPath" alt="" class="img_placeOrder_box">
			<p class="placeOrder_box_text">
				<span>{{ itemData.commodityName }}</span>
				<span class="color_ff588c">{{ itemData.amount }}福利值</span>
			</p>
		</div>
		<div class="placeOrder_box2">
			<van-divider  :style="{ margin: '0', padding: '0' }" :hairline="false"/>
			<p>
				<span>数量</span>
				<span>x1</span>
			</p>
			<p>
				<span>合计</span>
				<span class="color_ff588c">{{ itemData.amount }}福利值</span>
			</p>
			<van-divider  :style="{ margin: '0', padding: '0' }" :hairline="false"/>
		</div>
		<p class="btn_submit" @click="addExchangeOrder">提交订单</p>
		<p class="placeOrder_tips">【温馨提示】<br>话费抵扣券，仅支持在充值话费时进行支付抵扣，不支持直接充，如遇特殊情况请联系客服。</p>
	</div>
</template>

<script>
	import syfulizhi from '../components/syfulizhi.vue'
	export default{
		name:'placeOrder',
		components:{
			syfulizhi
		},
		data(){
			return {
				itemData:{}
			}
		},
		mounted() {
			let _this = this;
			var jsonObj = localStorage.getItem("obj");
			_this.obj = JSON.parse(jsonObj);
			this.itemData = this.$route.query.itemData;
		},
		methods:{
			goBack(){
				this.$router.go(-1);
			},
			addExchangeOrder(){
				let _this = this;
				var exclusiveObj = {
					commodityId:_this.itemData.id,
					type:_this.itemData.commodityType
				}
				var requestData = Object.assign(_this.obj,exclusiveObj);
				var data = _this.byte256.byte256.encode(_this.key, JSON.stringify(requestData));
				_this.$axios({
				  method: 'post',
				  url: '/commodity/order/addExchangeOrder',
				  data:data,
				}).then(function(res) {
					console.log(res);
				  if(res.data.code === 200){
					var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
					console.log(reData);
					_this.$router.push({path:'/exchangeResult',query:{itemData:reData}});
				  }else{
					  _this.$toast(res.data.msg);
				  }
				});
			}
		}
		
	}
</script>

<style scoped>
	.placeOrder{
		width: 10.8rem;
		min-height: 100vh;
		background-color: #f4f4f4;
	}
	.placeOrder_head{
		width: 100%;
		height: 1.51rem;
		position: relative;
		background-color: #fff;
		padding-top:var(--barHeight);
	}
	.icon_arrow_left_black{
		display: inline-block;
		vertical-align: middle;
		width:0.56rem;
		height:0.56rem;
		background-image: url(../static/img/icon_arrow_left_black.png);
		background-repeat: no-repeat;
		background-size: 100%;
		position: relative;
		z-index: 4;
		margin:0.47rem 0 0 0.33rem;
	}
	.placeOrder_head p{
		width: 100%;
		height: 1.51rem;
		line-height: 1.51rem;
		position: absolute;
		bottom:0;
		left:0;
		z-index: 2;
		text-align: center;
		color:#000;
		font-size: 0.48rem;
	}
	.placeOrder_box{
		width: 100%;
		box-sizing: border-box;
		padding: 0 0.33rem;
		display: flex;
		justify-content: space-between;
		margin-top:0.44rem;
	}
	.img_placeOrder_box{
		width: 1.8rem;
		height: 1.8rem;
	}
	.placeOrder_box_text{
		width: 8.1rem;
		font-size: 0.48rem;
		color:#232426;
		line-height: 0.94rem;
	}
	.placeOrder_box_text span{
		display: inline-block;
		width: 100%;
	}
	.placeOrder_box2{
		color:#000;
		font-size: 0.36rem;
		line-height: 1.2rem;
		box-sizing: border-box;
		padding: 0.33rem;
	}
	.placeOrder_box2 p{
		display: flex;
		justify-content: space-between;
		
	}
	.btn_submit{
		width: 10.14rem;
		height: 1.2rem;
		border-radius: 0.6rem;
		font-size: 0.48rem;
		color:#fff;
		text-align: center;
		line-height: 1.2rem;
		background-color: #ff588c;
		margin:0.7rem auto;
	}
	.placeOrder_tips{
		box-sizing: border-box;
		padding:0 0.33rem;
		font-size: 0.36rem;
		color:#848383;
		line-height: 0.64rem;
	}
</style>
